@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
/* ---------------------------FONTS-------------------------- */

.sdf-font {
  font-family: "Roboto", sans-serif;
}

.sdf-title {
  font-family: "Roboto", sans-serif;
  font-size: 72px;
  font-weight: 900;
  font-style: italic;
}

.sdf-h1 {
  font-family: "Roboto", sans-serif;
  font-size: 48px;
  font-weight: 900;
  font-style: italic;
}

.sdf-h2 {
  font-family: "Roboto", sans-serif;
  font-size: 36px;
  font-weight: 900;
  font-style: italic;
}

.sdf-h3 {
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 900;
  font-style: italic;
}

.sdf-h4 {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 900;
  font-style: italic;
}

.sdf-h5 {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  font-weight: 900;
  font-style: italic;
}

/* --------------------------LIGHT AND DARK------------------------------ */

.dark {
  color: white;
}

.light {
  color: black;
}

/* ---------------------------SUBMIT BUTTONS------------------------------- */

.sdf-s-submit {
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 900;
  font-style: italic;
  background-color: #e41749;
  color: white;
  border: none;
  outline: none;
  height: 50px;
  width: 150px;
}

.sdf-m-submit {
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  font-weight: 900;
  font-style: italic;
  background-color: #e41749;
  color: white;
  border: none;
  outline: none;
  width: 300px;
  height: 68px;
  margin: 10px;
}

.sdf-l-submit {
  font-family: "Roboto", sans-serif;
  font-size: 36px;
  font-weight: 900;
  font-style: italic;
  background-color: #e41749;
  color: white;
  border: none;
  outline: none;
  height: 68px;
  width: 358px;
}

/* ----------------------------------INPUT FORM----------------------------- */
.sdf-form {
  width: 300px;
  height: 50px;
  padding: 20px;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  border-radius: none;
  border: 1px solid lightslategray;
  outline: none;
  margin: 10px;
}

.sdf-form-code {
  width: 60px;
  height: 60px;
  padding: 10px;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  border-radius: none;
  border: 1px solid lightslategray;
  outline: none;
  margin: 10px;
}

.sdf-form-search {
  padding: 10px;
  min-width: 200px;
  width: 300px;
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  border-radius: 6px;
  outline: none;
  background-color: whitesmoke;
  border: none;
}

/* ---------------------------CARD-------------------- */
.sdf-card {
  display: flex;
  flex-wrap: wrap;
  width: 320px;
  height: 400px;
  background: white;
  border: 1px solid gray;
}

* {
  margin: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
/* ------------------------------Link------------------------ */
a {
  text-decoration: none;
  color: #9e9e9e;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
}

a:hover {
  color: #e41749;
}

/* ----------------------------Login Container------------------ */

.login-container {
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login-container > p {
  padding: 20px;
  margin: 10px;
}

.login-container > div {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 300px;
  height: 68px;
  margin: 5px;
  font-size: 14px;
}
/* ------------------------------------Codebar input------------------------------------- */
.login-container > .code-bar {
  display: flex;
  flex-direction: row;
  width: auto;
  height: auto;
}

/* ----------------------------------------------HOME---------------------------------------------------------------- */

.container {
  display: flex;
  flex-wrap: wrap;
}

.navbar {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 20px 30px;
  justify-content: flex-end;
}

.title-container {
  width: 100%;
  padding: 80px 100px;
}

.search-container {
  width: 100%;
  padding: 10px;
  margin: 20px 100px;
  box-shadow: 0px 2px 5px lightgray;
}

/* --------------------------------------------CARDS----------------------------- */

.cards-container {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 100px;
  width: 100%;
  justify-content: space-between;
}

/* ------------------------------------------------Top-rating--page-------------------- */

.top-rating-container {
  display: flex;
  background-image: url(/league-skins/static/media/img.60fe8d74.jpg);
  margin: 100px 150px 20px 150px;
  width: 100%;
  height: 800px;
}

.sp-container {
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 800px;
  background: rgba(0, 0, 0, 0.637);
}

.sp-title {
  padding: 20px 20px;
  margin: 40px 40px 0 40px;
  border-bottom: 1px solid rgba(207, 207, 207, 0.322);
}

.sp-card-container {
  padding: 20px;
  overflow: scroll;
}

/* -------------------------------------Sidebar-Scrollbar-------------------- */

.sp-card-container::-webkit-scrollbar {
  width: 4px;
}

/* Handle */
.sp-card-container::-webkit-scrollbar-thumb {
  background: #757575;
}

/* Handle on hover */
.sp-card-container::-webkit-scrollbar-thumb:hover {
  background: #e41749;
}
/* ------------------------------------------Sidepanel Card----------------------------- */
.sp-card {
  display: flex;
  width: auto;
  height: 100px;
  margin: 20px;
  background-color: rgba(0, 0, 0, 0.548);
}

.pp1 {
  background-image: url(/league-skins/static/media/profile.18a1c553.jpg);
  height: 100px;
  width: 100px;
}

.sp-card-rating {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.sp-card-rating > .rating {
  padding: 10px 0;
  width: auto;
}

